<template>
    <form @submit.prevent="addNewPost">
        <h1>Добавить новый пост</h1>
        <b-form-input v-model="title" placeholder="Заголовок"></b-form-input>
        <b-form-textarea
        id="textarea"
        v-model="body"
        placeholder="статья"
        rows="3"
        max-rows="6"
        ></b-form-textarea>
        <b-button type="submit" variant="primary">Добавить новый пост</b-button>
        <hr>
    </form>
</template>

<script>
import {mapActions} from "vuex";
export default {
    
    data(){
        return{
            title: "",
            body: "",
        }
    },
    methods: {
        ...mapActions([
            "addPost",
        ]),
        addNewPost(){
            if(this.title.trim() && this.body.trim()){
                console.log(this.title + " and " + this.body);
                this.addPost({body: this.body, id: "", title: this.title})
            }
            this.body = this.title = "";
            
        },
        
        
    }
}
</script>

<style lang="scss" scoped>
    form {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        input, textarea {
            max-width: 500px;
            margin-top: 20px;
        }
        button {
            margin-top: 20px;
        }
    }
</style>